// frontend/app/layout.tsx

import './globals.css'; 

// This defines the metadata for the page (SEO, title, etc.)
export const metadata = {
  title: 'Daxter - Accountant Data Aggregator',
  description: 'Financial and tax compliance dashboard',
};

// This is the Root Layout Component
// It receives a 'children' prop, which will be the content of page.tsx
export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en">
      <body>
        {children} 
      </body>
    </html>
  );
}